<template>
	<div class="root">
		<Header />
		<Search 
			:searchQuery="searchQuery"
			@search-query="updateSearchQuery" 
			v-on:update-num-vis-effects="updateNumVisEffects" 
		/>
		<Results 
			:items="searchResults" 
			:allEffects="allEffects" 
			:numVisEffects="numVisEffects" 
			:selectedIngredient="selectedIngredient"
			v-on:ingredient-selected="ingredientSelected"
			v-on:ingredient-deselected="ingredientDeselected"
		/>
	</div>
</template>

<script>
import Header from './components/Header.vue'
import Search from './components/Search.vue'
import Results from './components/Results.vue'
import sourceData from './data/data.json';

export default {
	name: 'App',
	components: {
		Header,
		Search,
		Results
	},
	data () {
		const ret = {
			numVisEffects: 0,
			searchQuery: '',
			sourceData,
			searchResults: Object.keys(sourceData.ingredients).map(key => sourceData.ingredients[key]),
			selectedIngredient: ''
		};

		return ret;
	},
	computed: {
		allEffects () {
			return sourceData.effects;
		}
	},
	methods: {
		updateNumVisEffects (num) {
			this.numVisEffects = num;
			this.updateSearchResults();
		},
		updateSearchQuery (query) {
			this.searchQuery = query;
			this.updateSearchResults();
		},
		updateSearchResults () {
			if (this.searchQuery.length === 0) {
				this.searchResults = Object.keys(this.sourceData.ingredients).map(key => this.sourceData.ingredients[key]);
				return;
			}

			const results = [];
			const queries = this.searchQuery.replaceAll(', ', ',').toLowerCase().split(',');
			
			// match name
			for (let key in this.sourceData.ingredients) {
				const ingredient = this.sourceData.ingredients[key];
				for (let q = 0; q < queries.length; q++) {
					if (queries[q].length > 0 && ingredient.name.toLowerCase().includes(queries[q].trim())) {
						results.push(ingredient);
					}	
				}
			}

			// match effects
			for (let key in this.sourceData.ingredients) {
				const ingredient = this.sourceData.ingredients[key];

				for (let i = 0; i < ingredient.effects.length && i < this.numVisEffects; i++) {
					const effectKey = ingredient.effects[i];
					const effect = this.sourceData.effects[effectKey];

					for (let q = 0; q < queries.length; q++) {
						if (queries[q].length > 0 && effect.name.toLowerCase().includes(queries[q].trim()) && results.indexOf(ingredient) === -1) {
							results.push(ingredient);
						}
					}
				}
			}

			this.searchResults = results;
		},
		ingredientSelected (id) {
			this.selectedIngredient = id;

			if (this.numVisEffects === 0) {
				return;
			}

			const queries = [];
			const ingredient = this.sourceData.ingredients[this.selectedIngredient];

			for (var e = 0; e < ingredient.effects.length && e < this.numVisEffects; e++) {
				queries.push(this.sourceData.effects[ingredient.effects[e]].name.toLowerCase());
			}

			this.updateSearchQuery(queries.join(', '));
		},
		ingredientDeselected () {
			this.selectedIngredient = '';
		}
	}
}
</script>

