<template>
	<div :class="ingredientClass" @click="ingredientClick(id)">
		<div class="c-ingredient__content">
			<h3 class="c-ingredient__title">{{ title }}</h3>
			<img :src="imageSrc" alt="" class="c-ingredient__icon">
			<p class="c-ingredient__weight">Weight: <span>{{ weight }}</span></p>
			<p class="c-ingredient__value">Value: <span>{{ goldValue }}</span></p>
			<ul class="c-ingredient__effects">
				<li v-for="effect in effects" :key="effect.id">
					<Effect 
						:name="effect.name" 
						:image-name="effect.id"
					/>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import Effect from './Effect.vue';

const options = {
	classes: {
		base: 'c-ingredient',
		isSelected: 'is--selected'
	}
};

export default {
	name: 'Ingredient',
	components: {
		Effect
	},
	props: {
		id: String,
		title: String,
		imageName: String,
		weight: String,
		goldValue: String,
		effectKeys: Array,
		allEffects: Object,
		numVisEffects: {
			type: Number,
			default: 1
		},
		isSelected: {
			type: Boolean,
			default: false
		}
	},
	computed: {
		ingredientClass () {
			return `${options.classes.base}${this.isSelected ? ' ' + options.classes.isSelected : ''}`;
		},
		imageSrc () {
			return require(`../assets/img/ingredients/${this.imageName}.png`);
		},
		effects () {
			const effects = [];

			for (let i = 0; i < this.effectKeys.length; i++) {
				if (effects.length < this.numVisEffects && this.allEffects.hasOwnProperty(this.effectKeys[i])) {
					effects.push(this.allEffects[this.effectKeys[i]]);
				}
			}

			return effects;
		}
	},
	methods: {
		ingredientClick (id) {
			if (this.isSelected) {
				this.$emit('ingredient-deselected');
			} else {
				this.$emit('ingredient-selected', id);
			}
		}
	}
}
</script>

<style scoped lang="scss">
	@import '../styles/_variables.scss';

	.c-ingredient {
		$root: &;

		width: 100%;
		height: 100%;
		padding: $spacing-small 0 0 $spacing-small;

		&__content {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 0.5rem;
			background-color: $color-cod;
			height: 100%;
			overflow: hidden;
			border: 1px solid transparent;
			color: $color-equator;

			#{$root}.is--selected & {
				border-color: $color-white;
				color: $color-white;
			}
		}
		
		&__title {
			margin: 0;
			font-size: $font-size-small;
			text-align: center;
		}

		&__icon {
			width: 32px;
			height: 32px;
		}

		&__weight {
			margin: 0;
			font-size: $font-size-small;
		}

		&__value {
			margin: 0;
			font-size: $font-size-small;
		}

		&__effects {
			padding: 0;
			list-style: none;
		}
	}
</style>
