<template>
		<div class="c-results">
				<ul class="c-results__list">
					<li class="c-results__item" v-for="item in items" :key="item.id">
						<Ingredient 
							:id="item.id"
							:title="item.name"
							:imageName="item.id"
							:weight="item.weight"
							:goldValue="item.value"
							:effectKeys="item.effects"
							:allEffects="allEffects"
							:numVisEffects="numVisEffects"
							:isSelected="ingredientIsSelected(item.id)"
							v-on:ingredient-selected="ingredientSelected"
							v-on:ingredient-deselected="ingredientDeselected"
						/>
					</li>
				</ul>
		</div>
</template>

<script>
import Ingredient from './Ingredient.vue'

export default {
	name: 'Results',
	components: {
		Ingredient
	},
	props: {
		items: Array,
		allEffects: Object,
		numVisEffects: Number,
		selectedIngredient: String
	},
	methods: {
		ingredientIsSelected (id) {
			return this.selectedIngredient === id;
		},
		ingredientSelected (id) {
			this.$emit('ingredient-selected', id);
		},
		ingredientDeselected () {
			this.$emit('ingredient-deselected');
		}
	}
}
</script>

<style scoped lang="scss">
	@import '../styles/_variables.scss';

	.c-results {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		background-color: $color-black;
		margin-top: 0.5rem;
		height: calc(100vh - 6rem);
		border: 1px solid $color-equator;

		&__list {
			list-style: none;
			padding: 0 $spacing-small $spacing-small 0;
			margin: 0;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-items: flex-start;
			align-content: flex-start;
			overflow-y: scroll;
			width: 100%;
			height: 100%;
		}

		&__item {
			display: block;
			width: 33.333%;
			align-self: stretch;

			@media (min-width: $breakpoint-tablet) {
				width: 25%;
			}

			@media (min-width: $breakpoint-desktop) {
				width: 16.667%;
			}

			@media (min-width: $breakpoint-largedesktop) {
				width: 12.5%;
			}

			@media (min-width: $breakpoint-xlargedesktop) {
				width: 10%;
			}
		}
	}
</style>
