<template>
	<div class="c-search">
		<div class="c-search__input-wrapper">
			<input class="c-search__input" type="text" placeholder="Ingredient or effect" :value="searchQuery" v-on:keyup="emitSearchQuery" />
			<button class="c-search__clear" type="button" @click="clear">X</button>
		</div>
		<div class="c-search__filters">
			<h3 class="c-search__filters-title">Level</h3>
			<select class="c-search__filters-dropdown" @change="numVisibleEffects">
				<option class="c-search__filters-item" value="0">0 - 14</option>
				<option class="c-search__filters-item" value="1">15 - 29</option>
				<option class="c-search__filters-item" value="2">30 - 44</option>
				<option class="c-search__filters-item" value="3">45 - 59</option>
				<option class="c-search__filters-item" value="4">60 - 100</option>
			</select>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Search',
	props: {
		searchQuery: {
			type: String,
			default: ''
		}
	},
	methods: {
		emitSearchQuery (e) {
			this.$nextTick(() => {
				this.$emit('search-query', e.currentTarget.value.toLowerCase());
			})
		},
		numVisibleEffects (e) {
			this.$emit('update-num-vis-effects', parseInt(e.currentTarget.value));
		},
		clear () {
			this.$emit('search-query', '');
		}
	}
}
</script>

<style scoped lang="scss">
	@import '../styles/_variables.scss';

	.c-search {
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		&__input {
			width: 100%;
			background-color: $color-black;
			border: 1px solid $color-equator;
			padding: 0.25rem 0.5rem;
			font-size: $font-size-normal;
			font-family: $font-cursive;
			color: $color-white;
			font-weight: bold;
			letter-spacing: 1.5px;

			&:focus {
				outline: none;
				border-color: $color-white;
			}

			&::placeholder {
				color: $color-tundora;
			}

			&-wrapper {
				display: block;
				position: relative;
				width: 100%;
			}
		}

		&__clear {
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			background-color: $color-black;
			font-family: $font-cursive;
			font-size: 1rem;
			font-weight: bold;
			color: $color-equator;
			border: 1px solid $color-equator;
			line-height: 30px;
			width: 34px;
			text-align: center;
			z-index: 1;
		}

		&__filters {
			padding-left: $spacing-med;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;

			&-title {
				font-size: $font-size-normal;
				margin: 0 $spacing-med 0 0;
				color: $color-equator;
			}

			&-dropdown {
				font-family: $font-cursive;
				font-size: $font-size-normal;
				background-color: $color-black;
				color: $color-white;
				border: 1px solid $color-equator;
				height: 34px;
				line-height: 30px;

				&:focus {
					outline: none;
					border-color: $color-white;
				}
			}
		}
	}
</style>
