		</li>
</template>

<script>
export default {
	name: 'Effect',
	props: {
		name: String,
		imageName: String
	},
	computed: {
		imageSrc () {
			return require(`../assets/img/effects/${this.imageName}.jpg`);
		}
	}
}
</script>

